.home {
    background: #000;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.home input{
    height: 20px;
    width: 10rem;
}

.home button {
    height: 100%;
    border: none;
    width: 4rem;
    background: white;
    color: #000;
}